import axios from "axios";
// let BASE_URL = "http://127.0.0.1:8000/"
let BASE_URL = "https://combwacth.herokuapp.com/"

async function login(user) {
  try {
    const { data } = await axios.post(`${BASE_URL}auth/`, {
      "username": user.nome,
      "password": user.senha
    })
    localStorage.setItem('token', `${data.token}`);
    return {
      mensagem: "[SUCESSO] - Login efetuado",
      code: 200
    }
  } catch (error) {
    return {
      mensagem: `[ERRO] - ${error}`,
      code: error.status.code
    }
  }
}
async function Postos() {
  try {
    const { data } = await axios.get(`${BASE_URL}api/postos`, {
      headers: {
        Authorization: `JWT ${localStorage.getItem("token")}`
      }
    })
    return {
      mensagem: "[SUCESSO] - Postos listados",
      code: 200,
      data: data
    }
  } catch (error) {
    return {
      mensagem: "[ERROR] - Postos não listados",
      code: 400,
      data: error
    }
  }
}
async function Combustiveis() {
  try {
    const { data } = await axios.get(`${BASE_URL}api/combustiveis`, {
      headers: {
        Authorization: `JWT ${localStorage.getItem("token")}`
      }
    })
    return {
      mensagem: "[SUCESSO] - Combustiveis listados",
      code: 200,
      data: data
    }
  } catch (error) {
    return {
      mensagem: "[ERROR] - Combustiveis não listados",
      code: 400,
      data: error
    }
  }
}
async function Atualizador(id, valor) {
  try {
    await axios.patch(`${BASE_URL}api/combustiveis/${id}/`, { id: id, valor: valor }, {
      headers: { Authorization: `JWT ${localStorage.getItem("token")}` }
    })
    return {
      status: 200
    }
  } catch (error) {
    // console.log(error)
    return {
      status: 500
    }
  }
}
export { login, Postos, Combustiveis, Atualizador }